      setAxiosToken();
      const res = await axios.get(`${API.AUTH}/detailcategory`);
      console.log({ docCategory: res });
      dispatch<GetDocumentCategoryAction>({
        type: ActionTypes.GET_DOCUMENT_CATEGORY,
        payload: res.data,
      });
