// REMOVE DUPLICATION IN CANDIDATES IN EDUCATION CATEGORY PER SESSION
const distinctCandidates = (
  candidatesData: CandidatesInterface[]
): CandidatesInterface[] => {
  let result: CandidatesInterface[] = [];
  let user_ids: string[] = [];
  const newArray: CandidatesInterface[] = candidatesData.filter((candidate) => {
    return candidate !== null && candidate !== undefined;
  });
  newArray.forEach((candidate) => {
    if (!user_ids.includes(candidate.user_id)) {
      result.push(candidate);
      user_ids.push(candidate.user_id);
    }
  });
  return result;
};
// CANDIDATE IN EDUCATION CATEGORY PER SESSION
export const getCandidatesInInstitute = (
  educatin_type_id: string,
  session_id: string,
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "", null);
    try {
      setAxiosToken();
      const res = await axios.get<CandidatesInterface[]>(
        `${API.APPLICATION}/educationtypeuserdocs/${educatin_type_id}/${session_id}`
      );

      console.log("====================================");
      console.log({
        getCandidatesInInstitute: res,
        distinctCandidates: distinctCandidates(res.data),
      });
      console.log("====================================");
      dispatch<GetCandidatesInInstituteAtion>({
        type: ActionTypes.GET_CANDIDATE_IN_INSTITUTE,
        payload: distinctCandidates(res.data),
        // payload: response.data,
      });
      callback(false, "", distinctCandidates(res.data));
    } catch (error) {
      console.log({ getCandidatesInInstitute: error });
      callback(false, errorToText(error), null);
    }
  };
};

