type TableDataType = {
  post_id: string;
  school_id: string;
  school_name: string;
  position_id: string;
  position_name: string;
  // NUMBER OF FEMALE PROVIDED BY HT
  female: number;
  // NUMBER OF MALE PROVIDED BY HT
  male: number;
  // NUMBER OF POST REQUESTED BY HT
  requested_post: number;
  // NUMBER OF POST APPROVED BY DDE
  distributed_post: number;
  // NUMBER OF ALL POST (APPROVED + FEMALE + MALE)
  total_post: number;
  // NUMBER OF ALL POST (RECEIVED + FEMALE + MALE)
  total_staff: number;
  // NUMBER OF ALL RECEIVED POST
  new_teacher: number;
  // NUMBER OF ALL (APPROVED - RECEIVED) POST
  vacant_post: number;
  status: PostRequestStatusInterface;
};

type ConfirmedPostType = {
  requested_post_id: string;
  confirmed_posts: number;
  confirmed_by: string;
  approval_date: Date;
  description: string;
  requested_post_status: PostRequestStatusInterface;
};

type AddRequest4PlacementType = {
  school_id: string;
  school_name: string;
  request_id: string;
  district_id: string;
  sector_id: string;
  province_id: string;
  gender: "M" | "F";
  available_places: {
    position_id: string;
    requested_teacher: number;
  }[];
};

