  assignConfirmedPost = (
    thisPost: TableDataType,
    value: number,
    postRequests: TableDataType[]
  ) => {
    let data: ConfirmedPostType[] = [];
    const limit = this.getLimitations(
      postRequests,
      this.props.districtPlacementLimits!,
      thisPost.post_id
    );
    if (
      value > thisPost.requested_post ||
      limit.distributed + value > limit.general
    )
      return this.props.PushPopupAlert({
        title: `Incorrect value!`,
        theme: PopupAlertTheme.warning,
      });
    if (this.state.confirmedPosts) {
      data = [...this.state.confirmedPosts];
      const postIndex = data.findIndex(
        (item) => item.requested_post_id === thisPost.post_id
      );
      if (postIndex >= 0) {
        data[postIndex].confirmed_posts = value;
        data[postIndex].approval_date = new Date();
      } else {
        data.push({
          requested_post_id: thisPost.post_id,
          confirmed_posts: value,
          confirmed_by: this.props.auth.user.user_id,
          approval_date: new Date(),
          description: "Not defined",
          requested_post_status: PostRequestStatusInterface.CONFIRMED,
        });
      }
    } else {
      data.push({
        requested_post_id: thisPost.post_id,
        confirmed_posts: value,
        confirmed_by: this.props.auth.user.user_id,
        approval_date: new Date(),
        description: "Not defined",
        requested_post_status: PostRequestStatusInterface.CONFIRMED,
      });
    }
    this.setConfirmedPosts(data);
  };

  getAssignedValuePost = (postId: string): number => {
    let result: number = 0;
    if (this.state.confirmedPosts) {
      const thisPost = this.state.confirmedPosts.find(
        (item) => item.requested_post_id === postId
      );
      if (thisPost) result = thisPost.confirmed_posts;
    }
    return result;
  };

  loadData = (sessionId: string) => {
    if (!this.props.document.documentCategory) {
      this.props.getDocumentCategory();
    }
    if (!this.props.document.documentSubCategory) {
      this.props.getDocumentSubCategory();
    }
    if (this.props.systemPositions === null) {
      this.setLoading(true);
      this.props.getPositions((status: boolean, msg: string) => {
        this.setLoading(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }
    if (!this.props.districtPosts.length && this.state.loadingPost) {
      this.props.fetchDistrictPosts(
        this.props.auth.user_access.district_id,
        (status: boolean, message: string) => {
          this.setLoadingPost(status);
          if (!status) {
            if (message.length > 0 && this.state.loadingPost) {
              console.error("error loading school post: ", message);
              this.props.PushPopupAlert({
                title: message,
                theme: PopupAlertTheme.danger,
              });
            }
          }
        }
      );
    } else if (this.state.loadingPost) {
      this.setLoadingPost(false);
    }

    if (!this.props.districtPlacements) {
      this.props.getDistrictPlacement(
        this.props.auth.user_access.district_id,
        (status: boolean, message: string) => {
          this.setLoading(status);
          if (!status) {
            if (message.length > 0 && status) {
              console.error("error loading school post: ", message);
              this.props.PushPopupAlert({
                title: message,
                theme: PopupAlertTheme.danger,
              });
            }
          }
        }
      );
    } else if (this.state.loading) {
      this.setLoading(false);
    }
    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.setLoading(true);
      this.props.loadEducationCategories(this.setLoading);
    }
    if (!this.props.districtPlacementLimits) {
      this.props.getDistrictPlacementLimit(
        this.props.auth.user_access.district_id,
        sessionId,
        (status: boolean, message: string) => {
          this.setLoading(status);
          if (!status) {
            if (message.length > 0 && status) {
              console.error(
                "error loading getDistrictPlacementLimit: ",
                message
              );
              this.props.PushPopupAlert({
                title: message,
                theme: PopupAlertTheme.danger,
              });
            }
          }
        }
      );
    } else if (this.state.loading) {
      this.setLoading(false);
    }
  };

  componentDidMount = () => {
    if (!isEmptyOrSpaces(this.props.auth.user_access.district_id)) {
      if (
        this.props.activeAcademicSession === null ||
        this.props.activeAcademicSession === "none"
      ) {
        this.props.getActiveAcademicSession(
          (
            status: boolean,
            msg: string,
            data?: AcademicSessionInterface | "none"
          ) => {
            this.setLoading(status);
            if (msg.length > 0) {
              console.error({ loadingCandidatesError: msg });
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data && data !== "none") this.loadData(data._id);
          }
        );
      } else this.loadData(this.props.activeAcademicSession._id);
    }
  };

  doPlacement = async (thisPost: PostSchoolInterface) => {
    try {
      const res = await axios.post<any[]>(
        `${API.PLACEMENT}/doplace/${thisPost.school_id}/${thisPost.position_id}`,
        { request_id: thisPost._id }
      );

      if (res) {
        console.log("====================================");
        console.log({ doPlacement: res });
        console.log("====================================");
        this.props.fetchDistrictPosts(
          this.props.auth.user_access.district_id,
          (status: boolean, message: string) => {
            if (!status) {
              if (message.length > 0 && this.state.loadingPost) {
                console.error("error loading school post: ", message);
                this.props.PushPopupAlert({
                  title: message,
                  theme: PopupAlertTheme.danger,
                });
              }
              this.setLoadingPost(false);
            }
          }
        );

        let msgAlert: string = "";
        if (res.data.length > 0) {
          if (res.data.length === this.state.approvedPosts) {
            msgAlert = "All confirmed posts has been placed on school!";
          } else {
            msgAlert = `${res.data.length} of ${this.state.approvedPosts} post(s) has been placed on school!`;
          }
        } else {
          msgAlert = "Post request has been confirmed but no placement done!";
        }

        this.setThisPost(null);
        this.setApprovedPosts(0);
        this.setModalState(null);
        this.setThisPostTable(null);
        this.setTableColumns(defaultColumns);
        this.setSelectedPostId("");
        this.setConfirmedPosts(null);
        this.props.PushPopupAlert({
          title: msgAlert,
          theme:
            res.data.length > 0
              ? PopupAlertTheme.success
              : PopupAlertTheme.info,
        });
      }
    } catch (error) {
      console.error({ doPlacement_reply: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  reDoPlacement = async (thisPost: PostSchoolInterface) => {
    try {
      const res = await axios.post<any[]>(
        `${API.PLACEMENT}/doplace/${thisPost.school_id}/${thisPost.position_id}`,
        { request_id: thisPost._id }
      );

      if (res) {
        console.log("====================================");
        console.log({ reDoPlacement: res });
        console.log("====================================");
        this.props.fetchDistrictPosts(
          this.props.auth.user_access.district_id,
          (status: boolean, message: string) => {
            if (!status) {
              if (message.length > 0 && this.state.loadingPost) {
                console.error("error loading school post: ", message);
                this.props.PushPopupAlert({
                  title: message,
                  theme: PopupAlertTheme.danger,
                });
              }
              this.setLoadingPost(false);
            }
          }
        );

        let msgAlert: string = "";
        if (res.data.length > 0) {
          if (res.data.length === this.state.approvedPosts) {
            msgAlert = "All confirmed posts has been placed on school!";
          } else {
            msgAlert = `${res.data.length} of ${
              thisPost.occupied_posts
                ? thisPost.occupied_posts -
                  (thisPost.received_posts ? thisPost.received_posts : 0)
                : 0
            } post(s) has been placed on school!`;
          }
        } else {
          msgAlert =
            "Placement failed, Please wait for the next application period!";
        }

        this.props.PushPopupAlert({
          title: msgAlert,
          theme:
            res.data.length > 0
              ? PopupAlertTheme.success
              : PopupAlertTheme.info,
        });
      }
    } catch (error) {
      console.error({ doPlacement_reply: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  generateDataForSchoolRequest = (
    confirmedPosts: ConfirmedPostType[]
  ): { requests: AddRequest4PlacementType[]; posts: PostSchoolInterface[] } => {
    let school_ids: string[] = [],
      requests: AddRequest4PlacementType[] = [],
      posts: PostSchoolInterface[] = [];

    if (this.props.districtPosts) {
      confirmedPosts.forEach((confirmed) => {
        const thisPost = this.props.districtPosts.find(
          (item) => item._id === confirmed.requested_post_id
        );
        if (thisPost) {
          posts.push(thisPost);
          if (school_ids.includes(thisPost.school_id)) {
            const schoolIndex = requests.findIndex(
              (item) => item.school_id === thisPost.school_id
            );
            const positionIndex = requests[
              schoolIndex
            ].available_places.findIndex(
              (item) => item.position_id === thisPost.position_id
            );
            if (positionIndex < 0) {
              requests[schoolIndex].available_places.push({
                position_id: thisPost.position_id,
                requested_teacher: confirmed.confirmed_posts,
              });
            }
          } else
            requests.push({
              school_id: thisPost.school._id,
              school_name: thisPost.school.school_name,
              request_id: thisPost._id,
              district_id: thisPost.district_id,
              sector_id: thisPost.school.sector_id,
              province_id: thisPost.school.province_id,
              gender: thisPost.male_number > thisPost.female_number ? "F" : "M",
              available_places: [
                {
                  position_id: thisPost.position_id,
                  requested_teacher: confirmed.confirmed_posts,
                },
              ],
            });
        }
      });
    }

    return { requests, posts };
  };

